#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ui.form.container {
  width: 500px;
  padding: 30px 100px;

  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 2px 15px rgba(100, 54, 201, 0.5);

  display: flex;
  flex-direction: column;

  transition: all 0.2s;
}

h1 {
  text-align: center;
}

button.ui.violet.button {
  width: 50%;
  height: 50px;
  align-self: center;
  margin-top: 20px;
  font-size: 20px;
}

.message {
  text-align: center;
  margin-top: 20px;
}

.red {
  color: rgb(197, 0, 0);
}

.ui.active.inverted.dimmer {
  color: #6436c9;
}

.amount {
  font-size: 1.5em;
  font-weight: bold;
  color: rgb(54, 54, 54);
}

.currency {
  color: rgb(145, 145, 145);
}
